import React, { useState } from "react";
import useGetSavedProducts from "../../hooks/useGetSavedProducts";
import useGetTemplates from "../../hooks/useGetTemplates";
import { REACT_APP_HOST_HOOKS } from "../../config/config";
import { FiTrash, FiArrowUp, FiArrowDown } from "react-icons/fi";
import Modal from "react-modal";
import { toast } from "react-toastify";
import { ClipLoader } from "react-spinners";
import "react-toastify/dist/ReactToastify.css";
import "../../styles/products/savedProducts.css";


const SavedProducts = () => {
  const { products, error, loading, reloadProducts } = useGetSavedProducts();
  const { templates, error: templatesError, loading: templatesLoading } = useGetTemplates();
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [selectedProductId, setSelectedProductId] = useState(null);
  const [selectedTemplateIds, setSelectedTemplateIds] = useState([]);
  const [isProcessing, setIsProcessing] = useState(false);
  const [searchTerm, setSearchTerm] = useState('');

  const openModal = (productId) => {
    setSelectedProductId(productId);
    setModalIsOpen(true);
  };

  const closeModal = () => {
    setModalIsOpen(false);
    setSelectedProductId(null);
    setSelectedTemplateIds([]);
  };

  const handleDeleteTemplate = async (productId, templateId) => {
    try {
      setIsProcessing(true);
      const response = await fetch(`${REACT_APP_HOST_HOOKS}/api/products/${productId}/templates/${templateId._id}`, {
        method: "DELETE",
      });

      if (!response.ok) {
        throw new Error("Error al eliminar la plantilla.");
      }

      toast.success("Plantilla eliminada correctamente.");
      await reloadProducts();
    } catch (error) {
      toast.error(error.message);
    } finally {
      setIsProcessing(false);
    }
  };

  const handleAddTemplates = async () => {
    if (selectedTemplateIds.length === 0) {
      toast.warning("Por favor, selecciona al menos una plantilla.");
      return;
    }

    try {
      setIsProcessing(true);
     
      const response = await fetch(`${REACT_APP_HOST_HOOKS}/api/products/${selectedProductId}/assign-template`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ templateIds: selectedTemplateIds }),
      });

      if (!response.ok) {
        throw new Error("Error al agregar la(s) plantilla(s).");
      }

      toast.success("Plantilla(s) añadida(s) correctamente.");
      closeModal();
      await reloadProducts();
    } catch (error) {
      toast.error(error.message);
    } finally {
      setIsProcessing(false);
    }
  };

  const handleMoveTemplate = async (productId, templateId, direction) => {
    try {
      const response = await fetch(
        `${REACT_APP_HOST_HOOKS}/api/products/${productId}/templates/reorder`,
        {
          method: "PATCH",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ templateId, direction }),
        }
      );
  
      if (!response.ok) {
        throw new Error("Error al reordenar la plantilla.");
      }
  
      toast.success("Orden actualizado correctamente.");
      // Recargar productos para actualizar la vista
      await reloadProducts();
    } catch (error) {
      toast.error(error.message);
    }
  };

  const toggleTemplateSelection = (templateId) => {
    setSelectedTemplateIds((prevSelected) => {
      if (prevSelected.includes(templateId)) {
        return prevSelected.filter((id) => id !== templateId);
      } else {
        return [...prevSelected, templateId];
      }
    });
  };

  if (loading) return <ClipLoader color="#007bff" size={50} className="spinner" />;
  if (error) return <p className="error-text">Error: {error}</p>;

  const filteredProducts = products.filter(product => 
   product.title.toLowerCase().includes(searchTerm.toLowerCase())
  );

  return (
    <div className="saved-products-container">
      <h1 className="page-title">Productos Guardados</h1>

      <div className="template-search-container">
        <input
          type="text"
          placeholder="Buscar por nombre..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="template-search-input"
        />
      </div>

      <div className="container-items-saved">

        {filteredProducts.map((product) => (
          <div key={product.id} className="product-item-save-products">
            <h3 className="product-title">{product.title}</h3>
            <p className="product-id">ID Producto: {product.id}</p>
            <h4 className="templates-title">Plantillas asignadas:</h4>
            <ul className="template-list-asign">
              {product.templates.map((template, index) => (

                <li key={template.templateId} className="template-item-products-save">
                  <div className="template-info">
                    <strong>{template.name}</strong>
                  </div>

                  <div className="container-buttons-products-saved">

                    <div className="move-buttons">
                        <button
                          className="move-template-btn"
                          onClick={() => handleMoveTemplate(product.id, template.templateId, "up")}
                          disabled={index === 0}
                          title="Mover hacia arriba"
                        >
                          <FiArrowUp size={16} color="#007bff" />
                        </button>
                        <button
                          className="move-template-btn"
                          onClick={() => handleMoveTemplate(product.id, template.templateId, "down")}
                          disabled={index === product.templates.length - 1}
                          title="Mover hacia abajo"
                        >
                          <FiArrowDown size={16} color="#007bff" />
                        </button>
                      </div>

                      <button
                        className="delete-template-btn"
                        onClick={() => handleDeleteTemplate(product.id, template.templateId)}
                      >
                        <FiTrash size={20} color="#ff4d4d" />
                      </button>

                  </div>

                </li>
              ))}
            </ul>
            <button className="open-modal-btn" onClick={() => openModal(product.id)}>
              Asignar Plantilla
            </button>
          </div>
        ))}
      </div>

      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        className="modal-content"
        overlayClassName="modal-overlay"
      >
        <h2>Seleccionar Plantillas</h2>
        {templatesLoading ? (
          <ClipLoader size={20} color="#007bff" />
        ) : templatesError ? (
          <p className="error-text">Error: {templatesError}</p>
        ) : (
          <div className="templates-checkbox-list">
            {templates.map((template) => (
              <label key={template._id} className="template-checkbox-label">
                <input
                  type="checkbox"
                  value={template._id}
                  checked={selectedTemplateIds.includes(template._id)}
                  onChange={() => toggleTemplateSelection(template._id)}
                />
                {template.name}
              </label>
            ))}
          </div>
        )}
        <div className="modal-buttons">
          <button className="add-template-btn" onClick={handleAddTemplates} disabled={isProcessing}>
            {isProcessing ? <ClipLoader size={15} color="white" /> : "Agregar"}
          </button>
          <button className="close-modal-btn" onClick={closeModal}>
            Cerrar
          </button>
        </div>
      </Modal>
    </div>
  );
};

export default SavedProducts;
