import React from "react";
import { REACT_APP_HOST_HOOKS } from "../../config/config.js";
import '../../styles/login/loginButton.css'

const LoginButton = () => {
  const handleLogin = async () => {
    try {
  
      const response = await fetch(`${REACT_APP_HOST_HOOKS}/api/auth/url`);
 
      const data = await response.json();

      if (data.authUrl) {
        window.location.href = data.authUrl; 
      } else {
        console.error("No se pudo obtener la URL de autenticación.");
      }

    } catch (error) {
      console.error("Error al iniciar sesión:", error.message);
    }
  };

  return (
    <button className="login-btn" onClick={handleLogin}>
      Iniciar sesión con Mercado Libre
    </button>
  );
};

export default LoginButton;
