import { useState, useEffect } from "react";
import { REACT_APP_HOST_HOOKS } from "../config/config.js";

const useGetSavedProducts = () => {
  const [products, setProducts] = useState([]);
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(true);

  const fetchSavedProducts = async () => {
    setLoading(true);
    try {
      const response = await fetch(`${REACT_APP_HOST_HOOKS}/api/products/saved`);
      if (!response.ok) {
        throw new Error("Error al obtener los productos guardados.");
      }
      const data = await response.json();
      setProducts(data);
    } catch (error) {
      setError(error.message);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchSavedProducts();
  }, []);

  return { products, error, loading, reloadProducts: fetchSavedProducts };
};

export default useGetSavedProducts;
