import React, { useState, useEffect } from "react";
import { REACT_APP_HOST_HOOKS } from "../../config/config";
import '../../styles/templates/templateModal.css';

const TemplateModal = ({ product, closeModal }) => {
  const [templates, setTemplates] = useState([]);
  const [selectedTemplates, setSelectedTemplates] = useState([]);

  useEffect(() => {
    const fetchTemplates = async () => {
      try {
        const response = await fetch(`${REACT_APP_HOST_HOOKS}/api/templates`);
        const data = await response.json();
        setTemplates(data);

        // Marcar como seleccionadas las plantillas ya asignadas al producto
        if (product.templates) {
          const assignedTemplateIds = product.templates.map(t => t.templateId);
          setSelectedTemplates(assignedTemplateIds);
        }
      } catch (error) {
        console.error("Error al cargar las plantillas:", error);
      }
    };
    fetchTemplates();
  }, [product.templates]);

  const handleTemplateSelection = (templateId) => {
    if (selectedTemplates.includes(templateId)) {
      setSelectedTemplates(selectedTemplates.filter((id) => id !== templateId));
    } else {
      setSelectedTemplates([...selectedTemplates, templateId]);
    }
  };

  const handleAssignTemplates = async () => {
    try {
      const response = await fetch(`${REACT_APP_HOST_HOOKS}/api/products/${product.id}/assign-templates-modal`, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ templateIds: selectedTemplates, productAsign: product.title }),
      });
      if (response.ok) {
        closeModal();
      } else {
        console.error("Error al asignar plantillas");
      }
    } catch (error) {
      console.error("Error al asignar las plantillas:", error);
    }
  };

  return (
    <div className="template-manager-modal">
      
      <div className="template-manager-content">
        <h2 className="template-manager-title">Asignar plantillas a {product.title}</h2>
      
        <h3 className="template-manager-subtitle">Plantillas disponibles</h3>
        <ul className="template-manager-list">
          {templates.map((template) => (
            <li key={template._id} className="template-manager-item">
              <input
                type="checkbox"
                checked={selectedTemplates.includes(template._id)}
                onChange={() => handleTemplateSelection(template._id)}
                className="template-manager-checkbox"
              />
              <strong>{template.name}:</strong> {template.content}
            </li>
          ))}
        </ul>
        
        <div className="container-modal-product-list">
          <button onClick={handleAssignTemplates} className="template-manager-btn template-manager-btn-asign">Asignar Plantillas</button>
          <button className="template-manager-btn template-manager-btn-close" onClick={closeModal}>
            Cerrar
          </button>
        </div>

      </div>
    </div>
  );
};

export default TemplateModal;
