import { useEffect } from "react";
import { REACT_APP_HOST_HOOKS } from "../config/config.js";

const useAuthCheck = (setIsAuthenticated) => {

  useEffect(() => {
    const checkAuth = async () => {
      try {
        const response = await fetch(`${REACT_APP_HOST_HOOKS}/api/auth/check`, {
          method: "GET",
          credentials: "include", 
          headers: {
            "Content-Type": "application/json",
            "X-Requested-With": "XMLHttpRequest", // Prevención contra CSRF
          },
          mode: "same-origin", // Rechaza solicitudes desde orígenes distintos
        });

        if (!response.ok) {
          console.error('No estas autenticado.')
        }

        const data = await response.json();

        if (data.isAuthenticated) {
          setIsAuthenticated(true);
        } else {
          setIsAuthenticated(false);
        }

        return
      } catch (error) {
        console.error("Error al comprobar autenticación:", error.message);
        setIsAuthenticated(false); 
      }
    };

    checkAuth();
  }, [setIsAuthenticated]);
};

export default useAuthCheck;
