import React, { useState } from "react";
import TemplateModal from "../templates/TemplateModal.jsx";
import useGetProducts from '../../hooks/useGetProducts.js'
import Modal from "react-modal";
import { ClipLoader } from "react-spinners";
import '../../styles/products/productList.css'

const ProductList = () => {
  const { products, error, loading } = useGetProducts();
  const [searchTerm, setSearchTerm] = useState('');
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedProduct, setSelectedProduct] = useState(null);

  const openModal = (product) => {
    setSelectedProduct(product);
    setIsModalOpen(true);
  };

  const closeModal = () => {
    setIsModalOpen(false);
    setSelectedProduct(null);
  };

  // Filtrar plantillas según el término de búsqueda.
  const filteredProducts = products.filter(product => 
   product.title.toLowerCase().includes(searchTerm.toLowerCase())
  )

  if (loading) return <ClipLoader color="#007bff" size={50} className="spinner" />;


  if (error) return <p className="error-text">Error: {error}</p>;

  return (
    <div className="product-list-container">
      <h1 className="product-list-title">Lista de Productos</h1>

      <div className="template-search-container">
        <input
          type="text"
          placeholder="Buscar por nombre..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="template-search-input"
        />
      </div>

      <ul className="product-list">
        {filteredProducts.map((product, index) => (
          <li key={index} className="product-item">
            <h3 className="product-title-list">{product.title}</h3>
            <p className="product-id-list">ID producto: {product.id}</p>
            <button className="assign-template-btn" onClick={() => openModal(product)}>
              Asignar plantilla
            </button>
          </li>
        ))}
      </ul>

      {isModalOpen && selectedProduct && (
        <Modal
            isOpen={isModalOpen}
            onRequestClose={closeModal}
            className="modal-content"
            overlayClassName="modal-overlay"
        >
            <button className="close-btn" onClick={closeModal}>X</button>
            <TemplateModal product={selectedProduct} closeModal={closeModal} />
        </Modal>
      )}
    </div>
  );
};

export default ProductList;

