import React, { useState } from "react";
import ProductList from "../products/ProductList.jsx";
import SavedProducts from "../products/SavedProducts.jsx";
import '../../styles/toggleComponent.css'
import TemplateManager from "../templates/TemplateManager.jsx";

const Dashboard = () => {
  const [currentComponent, setCurrentComponent] = useState(0);

  return (
    <div className="toggle-container">
      <div className="button-container">
        <button 
          className={`toggle-button ${currentComponent === 0 ? 'active' : ''}`} 
          onClick={() => setCurrentComponent(0)}
        >
          Productos de MELI
        </button>
        <button 
          className={`toggle-button ${currentComponent === 1 ? 'active' : ''}`} 
          onClick={() => setCurrentComponent(1)}
        >
          Productos ya asignados
        </button>
        <button 
          className={`toggle-button ${currentComponent === 2 ? 'active' : ''}`} 
          onClick={() => setCurrentComponent(2)}
        >
          Plantillas
        </button>
      </div>

      <div className="component-display">
        {currentComponent === 0 && (
          <div className="first-component">
            <ProductList />
          </div>
        )}
        {currentComponent === 1 && (
          <div className="second-component">
            <SavedProducts />
          </div>
        )}
        {currentComponent === 2 && (
          <div className="third-component">
            <TemplateManager />
          </div>
        )}

      </div>
    </div>
  );
};

export default Dashboard;